.result {
  background-color: @basic-grey-result;
  display: flex;
  justify-content: center;
  width: 100%;

  @media (min-width: @tablet-width) {
    width: 100%;
    background-color: @basic-white;
    border-top: 1px solid @special-grey-2;
  }

  @media (min-width: @desktop-width) {
    border: none;
  }
}

.result__wrapper-mini {
  width: 280px;

  @media (min-width: @tablet-width) {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    position: relative;
  }
}

.result__wrapper-desktop {
  @media (min-width: @desktop-width) {
    width: 100%;
    background-color: @basic-grey;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.result__title {
  font-family: "OswaldRegular", "Arial", sans-serif;
  font-style: normal;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  margin-top: 27px;
  margin-bottom: 31px;

  @media (min-width: @tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-top: 43px;
    margin-bottom: 63px;
    text-align: left;
    width: 708px;
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    margin-bottom: 76px;
    width: 1220px;
  }
}

.result__description {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: @special-dark-grey;
  margin: 0;
  margin-bottom: 25px;

  @media (min-width: @tablet-width) {
    width: 708px;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 54px;
  }

  @media (min-width: @desktop-width) {
    width: 436px;
    height: 96px;
    margin-bottom: 70px;
    margin-right: 784px;
    padding-top: 70px;
  }
}

.result__block-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: min-content min-content 1fr;
    column-gap: 42px;
    justify-items: right;
    margin-bottom: 255px;
    width: 708px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: auto auto;
    width: 366px;
    height: auto;
    justify-items: left;
    margin: 0;
    grid-column: ~"2/3";
    grid-row: ~"3/4";
    margin-right: 854px;
    padding-bottom: 72px;
  }
}

.result__block {
  position: relative;
  border: 1px solid @special-grey;
  width: 124px;
  height: 56px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (min-width: @tablet-width) {
    width: 162px;
    height: 72px;
  }
}

.result__block-item {
  font-family: "OswaldRegular", "Arial", sans-serif;
  font-style: normal;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 37px;
  }
}

.result__block-item-description {
  position: absolute;
  text-align: center;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  color: @special-dark-grey;
  margin: 0;
  width: 82px;
  height: 19px;
  bottom: -11px;
  left: 20px;
  background-color: @basic-grey-result;

  @media (min-width: @tablet-width) {
    font-size: 14px;
    line-height: 14px;
    width: 142px;
    height: 27px;
    background-color: @basic-white;
    bottom: -18px;
    left: 10px;
  }

  @media (min-width: @desktop-width) {
    background-color: @basic-grey;
  }
}

.result__price {
  font-family: "OswaldRegular", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  margin-top: 35px;
  margin-bottom: 42px;

  @media (max-width: @mobile-width-only) {
    grid-column: ~"1/-1";
  }

  @media (min-width: @tablet-width) {
    margin: 0;
    width: 174px;
    font-size: 20px;
    line-height: 30px;
    display: flex;
    text-align: right;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    grid-column: ~"1/3";
    width: 327px;
    margin: 0;
    margin-top: 55px;
  }
}

.result__block-hidden-space {
  display: none;

  @media (min-width: @desktop-width) {
    display: block;
    width: 63px;
  }
}

.result__slider {
  display: grid;
  justify-content: center;
  position: relative;

  @media (min-width: @tablet-width) {
    height: 462px;
    width: 100%;
    background-color: @basic-grey-result;
    display: flex;
    justify-items: center;
    align-items: flex-end;
  }

  @media (min-width: @desktop-width) {
    width: 579px;
    height: auto;
    background-color: @basic-grey;
    position: absolute;
    top: 490px;
    margin-left: 651px;
  }
}

.result__slider-before,
.result__slider-after {
  @media (min-width: @tablet-width) {
    display: none;
  }
}

.result__slider-image {
  display: flex;
  justify-self: center;
  margin: auto;
}

.result__slider-toggle-container {
  display: flex;
  justify-content: space-between;
  justify-self: center;
  align-items: center;
  width: 197px;
  height: 40px;
  margin-top: 23px;
  margin-bottom: 25px;
  margin-left: 5px;

  @media (min-width: @tablet-width) {
    width: 570px;
    margin: 0;
    margin-bottom: 64px;
    z-index: 2;
    position: relative;
  }

  @media (min-width: @desktop-width) {
    width: 570px;
    margin: 0;
    height: auto;
  }
}

.result__slider-beforeafter {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    position: absolute;
    top: -190px;
    right: 0;
  }

  @media (min-width: @desktop-width) {
    top: -550px;
    right: -35px;
  }
}

.result__slider-toggle {
  width: 84px;
  height: 14px;
  border: none;
}

.result__slider-control-mini {
  @media (min-width: @tablet-width) {
    display: none;
  }
}

.result__slider-control {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    margin-top: 8px;
    margin-left: 6px;
  }

  &::before {
    position: absolute;
    top: 10px;
    left: 73px;
    content: url("../img/slider-desktop.svg");
    z-index: -1;

    @media (min-width: @tablet-width) {
      top: 10px;
    }

    @media (min-width: @desktop-width) {
      top: 15px;
    }
  }
}

.result__slider-control-image {
  width: 40px;
  height: 40px;
  stroke: @special-grey;
  outline: none;

  &:hover,
  &:active,
  &:focus {
    filter: drop-shadow(0 4px 2px rgba(0, 0, 0, 0.19));
  }

  &:active,
  &:focus {
    stroke: @basic-green;
  }
}


.result__slider-text {
  font-family: "OswaldRegular", "Arial", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0;
  cursor: default;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
    border-bottom: 1px dotted @special-grey;

    &:hover {
      border-bottom: 1px solid @special-grey;
    }

    &:active {
      border: none;
    }
  }
}
