  }
}
@media (min-width: 1440px) {
  .page-header__logo-big {
    margin-top: 52px;
  }
}
.page-header__nav-toggle {
  position: absolute;
  padding: 0;
  margin: 0;
  top: 24px;
  left: 50%;
  margin-left: 117px;
  z-index: 2;
  display: block;
  width: 24px;
  height: 16px;
  cursor: pointer;
  border: none;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .page-header__nav-toggle {
    display: none;
  }
}
@media (max-width: 768px) {
  .page-header__nav-toggle--closed + .main-nav .main-nav__list {
    display: none;
    top: 66px;
  }
}
@media (max-width: 768px) {
  .page-header__nav-toggle--opened + .main-nav .main-nav__list {
    display: block;
    top: 66px;
  }
}
.page-header__nav-toggle--nojs.page-header__nav-toggle {
  display: none;
}
@media (max-width: 768px) {
  .page-header__nav-toggle--nojs + .main-nav .main-nav__list {
    position: static;
    width: 100%;
    top: 66px;
  }
}
.page-header__nav-toggle--closed::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (min-width: 768px) {
  .page-header__nav-toggle--closed::before {
    display: none;
  }
}
.page-header__nav-toggle--opened {
  width: 24px;
  height: 24px;
  margin-top: -5px;
}
.page-header__nav-toggle--opened::before,
.page-header__nav-toggle--opened::after {
  top: 12px;
  right: 0;
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.page-header__nav-toggle--opened::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header__nav-toggle--opened::after {
  transform: rotate(-45deg);
}
